.dialog-outer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.452);
  display: flex;
  align-items: center;
  justify-content: center;
}

.dialog-outer .video-dialog {
  display: grid;
  grid-template-columns: 1fr max-content;
  gap: 1em;
  align-items: center;
}

.dialog-outer .video-dialog iframe {
  border: none;
  outline: 1px solid rgba(255, 255, 255, 0.13);
  border-radius: var(--my-border-radius);
}

.dialog-outer .youtube {
  background-color: var(--my-secondary) !important;
  color: white !important;
}


.dialog {
  max-width: 600px;
  min-height: 400px;
  width: 100%;
  border-radius: 8px;
  background-color: var(--my-color-surface3);
  padding: 24px;
  font-size: var(--my-medium-font);
}

.dialog.mini {
  max-width: 400px;
  min-height: 300px;
}

.dialog .title {
  text-align: center;
  margin-top: 0;
  color: white;
  font-weight: normal;
  font-size: var(--my-average-font);
}

.dialog p.note {
  background-color: rgba(255, 255, 255, 0.164);
  padding: 16px 12px;
  border-radius: 8px;
  color: rgb(197, 197, 197);
  margin-bottom: 1.5em;
}

.dialog p.note a {
  color: white;
  font-weight: bold;
  text-decoration: underline;
}

.dialog button {
  border: none;
  border-radius: 6px !important;
}

.dialog button.danger {
  background-color: rgba(255, 0, 0, 0.164);
  color: var(--my-color-on-secondary);
}


.dialog .warning {
  text-align: center;
  margin: 4em 0;
  font-size: var(--my-medium-font);
  color: rgba(255, 255, 255, 0.753);
  line-height: 1.5em;
}

.dialog .col-2 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  gap: 1em;

}

.dialog .col-2 button {
  width: 100%;
}

.dialog input {
  font-size: var(--my-medium-font);
}



.dialog .input-image {
  width: 100px;
  height: 100px;
  position: relative;
  margin: 2em auto;
}

.dialog .input-image .edit-icon {
  width: 32px;
  height: 32px;
  background-color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: 0;
  bottom: 0;

}

.dialog .center-title {
  display: block;
  text-align: center;
  color: white;
  font-size: var(--my-average-font);
}


.dialog .input-image .edit-icon svg {
  fill: var(--my-primary);
  width: 16px;
  height: 16px;
}

.dialog .input-image input {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.001;
}

.dialog .input-image img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 1px solid rgba(255, 255, 255, 0.151);
  object-fit: cover;
}

.dialog .input-container {
  margin-bottom: 16px;
}

.dialog .input-container p.heading {
  color: rgba(255, 255, 255, 0.596);
  margin: 0;
  margin-top: 8px;
  margin-bottom: 8px;
}

.dialog .input-container .grid-auto-max {
  display: grid;
  grid-template-columns: auto max-content;
  gap: 12px;
  align-items: center;
}

.dialog .input-container .text-field {
  padding: 16px 12px;
  background-color: rgba(255, 255, 255, 0.041);
  border-radius: var(--my-border-radius);
  border: 1px solid rgba(255, 255, 255, 0.151);
  color: white;
  margin: 0;
  font-family: sans-serif;
}

.dialog .input-container .copy-btn {
  background-color: transparent;
  border: none;
  fill: white;
  background-color: rgba(255, 255, 255, 0.075);
  height: 55px;
  width: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: var(--my-border-radius);
}

.dialog .input-container .copy-btn svg {
  fill: white;
}

.dialog .submit-btn {
  margin-left: auto;
  padding: 12px 24px;
  background-color: white;
  color: black;
  border-radius: var(--my-border-radius);
  display: block;
  font-size: var(--my-medium-font);
}




.dialog .status-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid rgba(255, 255, 255, 0.178);
  background-color: var(--my-color-surface);
  padding: 12px;
  border-radius: var(--my-border-radius);
}

.dialog .status-card span {
  color: white;
}

.dialog .status-card .left,
.dialog .status-card .right {
  display: flex;
  align-items: center;
  gap: 16px;
}




@media screen and (max-width: 1000px) {
  .dialog-outer .video-dialog iframe {
    display: none;
  }

}



/* .dialog */