@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap);
header {
    background-color: var(--my-color-surface);
    
}

header .page {
    display: grid;
    grid-template-columns: -webkit-max-content auto  -webkit-max-content;
    grid-template-columns: max-content auto  max-content;
    justify-content: space-between;
    align-items: center;
    height: 80px;
}

header .center a {
    color: rgba(255, 255, 255, 0.685);
}

header .center a.active,
header .center a:hover {
    color: white;
}


header .logo {
    height: 40px;
}

header .profile-img{
    display: flex;
    align-items: center;
    justify-content: center;
}

header .profile-img img{
    border-radius: 50%;
    width: 40px;
    height: 40px;
    object-fit: cover;
}

header .center {
    display: flex;
    grid-gap: 16px;
    gap: 16px;
}

header button {
    padding: 0.8em 1.5em;
    border-radius: 50px;
    border: none;
}

header button.login{
    background-color: var(--my-primary);
}



header button.simple{
    background-color: white;
}


header .right{
    display: flex;
    grid-gap: 12px;
    gap: 12px;
    align-items: center;
}

header button.simple a {
    color: rgb(56, 56, 56);
    display: flex;
    align-items: center;
    grid-gap: 16px;
    gap: 16px;
}


header button.login a {
    color: white;
}



@media only screen and (max-width: 850px) {
    header .center {
        display: none;
    }

    header .page {
        grid-template-columns: -webkit-max-content -webkit-max-content;
        grid-template-columns: max-content max-content;
        height: 80px;
    }
}
footer {
    background-color: #181924;
    min-height: 300px;
}

footer .page{
    padding-top: 60px;
    padding-bottom: 60px;
}


footer .logo{
    display: block;
    margin: auto;
    height: 60px;
}

footer .links{
    display: flex;
    flex-wrap: wrap;
    grid-gap: 60px;
    gap: 60px;
    justify-content: center;
    margin-top: 50px;
    margin-bottom: 50px;
}


footer .links a{
    color: rgba(255, 255, 255, 0.733);
    text-transform: uppercase;
}

footer .links a:hover{
    color: white;
}



footer .socials{
    display: flex;
    grid-gap: 24px;
    gap: 24px;
    align-items: center;
    justify-content: center;
}

footer .socials>*:hover{
    opacity: 0.7;
}


footer .copyright{
    height: 120px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    grid-gap: 0.5em;
    gap: 0.5em;
    background-color: black;
    padding-left: 16px;
    padding-right: 16px;
}

footer .copyright p{
    margin: 0;
    text-align: center;
}


footer .copyright p.version{
    color: rgba(255, 255, 255, 0.562);
}





@media only screen and (max-width: 800px){
    footer .links{
        grid-gap: 24px;
        gap: 24px;
    }
}
.landing {}

.landing .page {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    margin-top: 80px;
    margin-bottom: 60px;
}


.landing h1 {
    font-size: 96px;
    font-family: 'Raleway' !important;
    font-weight: 900 !important;
    color: #FFF;
    margin-top: 0;
    margin-bottom: 0;
    line-height: 1em;
}

.landing h1 span {
    color: var(--my-pink);
}

.landing p {
    color: white;
    line-height: 2em;
}

.landing button {
    margin-top: 50px;
    margin-bottom: 40px;
    grid-gap: 6px;
    gap: 6px;
    display: flex;
    align-items: center;
    border-radius: 4px;
    padding: 1em 2em;
    border: none;
    color: white;
    font-weight: bold;
    font-size: var(--my-medium-font);
    background: linear-gradient(105deg, #AA07E0 36%, #F80282 90%, #FD309B 100%);
}


.landing .right {
    position: relative;
}



.landing .bot {
    width: 100%;
}

.landing .gradient {
    position: absolute;
    right: 10vw;
    top: -10vh;
    z-index: -1;
    opacity: 0.9;
}

.landing .phone{
    display: none;
}




@media only screen and (max-width: 1100px) {
    .landing h1 {
        font-size: 60px;
    }

    .landing .gradient {
        top: -15vh;
    }

}

@media only screen and (max-width: 900px){
    /* .landing .gradient {
        bottom: -100px;
    } */
}

@media only screen and (max-width: 800px) {
    .landing .gradient {
        display: none;
    }
    .landing .page {
        grid-template-columns: 100%;

    }

    .landing .phone{
        display: grid;
    }

    .landing .desktop{
        display: none;
    }

    .landing .gradient {
        bottom: 0px;
        opacity: 0.5;

    }

}




@media only screen and (max-width: 700px) {
    .landing .right {
        grid-template-columns: 2fr 1fr;
    }
}




@media only screen and (max-width: 550px) {
    .landing .gradient {
        bottom: -50px;
    }
}
.feature-section {
    margin-bottom: 100px;
    position: relative;
}


.feature-section .page {
    margin-bottom: 100px;
}

.feature-section .page.first {
    padding-top: 150px;
}

.feature-section .page.last {
    padding-bottom: 100px;
}



.feature-section .page>div {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 40px;
    gap: 40px;
    align-items: center;
    color: white;
}

.feature-section img {
    width: 100%;
}

.feature-section h2 {
    font-size: 56px;
    margin: 1em 0;
    font-weight: 900;
}

.feature-section p {
    font-size: 20px;
    margin: 0.6em 0;
}


.feature-section h2 span {
    color: var(--my-pink);
}

.feature-section .content div {
    display: flex;
    align-items: center;
    grid-gap: 1em;
    gap: 1em;
    line-height: 1.6em;
}

.feature-section .content div::before {
    content: url(/static/media/check.f76012e6.svg);

}

.feature-section .content.dot div::before {
    content: url(data:image/png;base64,);

}


@media only screen and (max-width: 1100px) {
    .feature-section h2 {
        font-size: 40px;
    }
}


@media only screen and (max-width: 800px) {
    /* .feature-section .page>div {
        grid-template-columns: 100%;
        gap: 0;
    } */

    .feature-section .page>div.center {
        display: flex;
        flex-direction: column-reverse;
        align-items: unset;
    }

    .feature-section .page>div.bottom {
        display: flex;
        flex-direction: column;
        align-items: unset;
    }

    .feature-section {
        margin-top: 100px;
        margin-bottom: 0;
    }


    .feature-section .page {
        margin-bottom: 40px;
    }

    .feature-section .page.first {
        padding-top: 0;
    }
    
    .feature-section .page.last {
        padding-bottom: 0;
    }
}
.integrate-section {
    margin-bottom: 50px;
    margin-top: 200px;
}


.integrate-section .card-holder {
    display: flex;
    align-items: center;
    grid-gap: 40px;
    gap: 40px;
    overflow-x: auto;
}


.integrate-section .card {
    width: 570px;
    min-width: 570px;
    min-height: 260px;
    padding: 30px 40px;
    padding-right: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

}

.integrate-section .card:first-child {
    --max-dd: max(var(--page-gap), ((100% - var(--max-page-width)) * 0.5));
    margin-left: var(--max-dd);
}

.integrate-section .card:last-child {
    --max-dd: max(var(--page-gap), ((100% - var(--max-page-width)) * 0.5));
    margin-right: var(--max-dd);
}


.integrate-section .card p {
    font-size: 20px;
    color: white;
    font-weight: 500;
}



.integrate-section .green {
    background-color: #38B647;
}

.integrate-section .green button div{
    background-color: #38B647;
}


.integrate-section .blue {
    background-color: #0063E2;
}

.integrate-section .blue button div{
    background-color: #0063E2;
}


.integrate-section .green-dark {
    background-color: #5A863E;
}

.integrate-section .green-dark button div{
    background-color: #5A863E;
}



.integrate-section .sky-blue {
    background-color: #00749A;
}

.integrate-section .sky-blue button div{
    background-color: #00749A;
}



.integrate-section a{
    text-decoration: none;
}

.integrate-section button {
    border-radius: 100px;
    background: rgba(234, 234, 236, 0.20);
    width: -webkit-max-content;
    width: max-content;
    border: none;
    outline: none;
    display: flex;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
    padding-left: 26px;
    cursor: pointer;
}

.integrate-section button:hover{
    background: rgba(234, 234, 236, 0.40);
}

.integrate-section button span{
    color: white;
    font-size: var(--my-medium-font);
    font-weight: bold;
}

.integrate-section button div{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 6px 3px;
}





.integrate-section .dots{
    margin-top: 40px;
    margin-bottom: 20vh;
    display: flex;
    grid-gap: 12px;
    gap: 12px;
    align-items: center;
    justify-content: center;
}


.integrate-section .dots span{
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #DBDBDB;
}

.integrate-section .dots span.active{
    background-color: #AA07E0;
}



@media only screen and (max-width: 700px) {
    .integrate-section.desktop{
        display: none !important;
    }
  }
  
.video-feature {
    /* margin-bottom: 100px; */
    position: relative;
    padding-top: 100px;
    padding-bottom: 100px;

}

.video-feature .video-container{
    position: relative;

}

.video-container .center{
    min-height: 650px;
}

.video-container .center img{
    width: 100%;
}

.video-feature .top{
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
}

.video-feature .phone{
    width: 100%;
    position: static;
    display: none;
    margin-top: 100px;
}


.video-feature .bottom-left{
    left: 0;
    bottom: 0;
}

.video-feature .top-right{
    right: 0;
    top: 0;
