}


.video-feature video {
    max-height: 500px;
    margin: auto;
    position: absolute;
}

.video-feature .fea {
    margin-top: 100px;
    display: grid;
    grid-template-columns: 1fr 2.5fr;
    grid-gap: 24px;
    gap: 24px;
