        display: block !important;
    }

    .video-feature {
        margin-bottom: 100px;
        padding-top: 0;
        padding-bottom: 0;
    
    }
}
/* chat area */

.chat-box.light {
  --my-color-surface2: white;
  --my-color-surface3: rgb(240, 240, 240);
  --my-color-surface: rgb(240, 240, 240);
  --my-power-color: rgb(143, 143, 143);
}

.chat-box {
  --my-power-color: rgb(172, 172, 172);
  font-family: "Inter", sans-serif;
}

.chat-box.light .header button {
  fill: black;
}

.chat-box.light .chat p {
  background-color: #f4f4f5;
}

.chat-box.light .default button {
  background-color: #f4f4f5;
  color: black;
}

.chat-box.light form {
  background-color: #f4f4f5;
}

.chat-box.light input {
  color: black;
}

.chat-box.light form button {
  fill: rgb(82, 82, 82);
}

/* leads */
.chat-box.light .lead {
  background-color: #f4f4f5;
}

.chat-box .lead .input-div {
  background-color: transparent !important;
}

.chat-box.light .lead form.input-div input {
  background-color: white;
  color: black;
}

.chat-box.light .lead button {
  background-color: black;
}

.chat-box.light .header div span {
  color: black;
}

.chat-box .header .logo {
  height: 32px;
}

.chat-box .header button svg {
  width: 20px;
  height: 20px;
  fill: inherit;
}

.chat-box form button img {
  height: 20px;
}

.chat-box {
  background-color: var(--my-color-surface2);
  display: grid;
  grid-template-rows: -webkit-max-content auto -webkit-max-content -webkit-max-content;
  grid-template-rows: max-content auto max-content max-content;
  height: 100vh;
  overflow: auto;
  border: 1px solid rgba(255, 255, 255, 0.14);
}

.chat-box.small {
  max-height: 700px;
}

.chat-box .header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--my-color-surface3);
  margin-bottom: 0;
  margin: 0 !important;
  height: 60px;
  padding: 0 24px;
}

.chat-box .header div {
  display: flex;
  align-items: center;
  grid-gap: 8px;
  gap: 8px;
}

.chat-box .header div img {
  height: 40px;
}

.chat-box .header div span {
  color: white;
  font-weight: 600;
  font-size: 18px;
}

.chat-box .header button {
  background-color: transparent;
  border: none;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background-color: var(--my-color-surface);
  display: flex;
  align-items: center;
  justify-content: center;
  fill: white;
}

.chat-box .box-content {
  display: flex;
  flex-direction: column;
  height: auto;
  min-height: 100px;
  overflow-y: auto;
  grid-gap: 8px;
  gap: 8px;
  padding: 24px;
  scroll-behavior: smooth;
}

.chat-box .default::-webkit-scrollbar {
  height: 3px;
}

.chat-box .box-content::-webkit-scrollbar {
  width: 6px;
}

/* Track */
.chat-box .box-content::-webkit-scrollbar-track,
.chat-box .default::-webkit-scrollbar-track {
  background: transparent;
}

/* Handle */
.chat-box .box-content::-webkit-scrollbar-thumb,
.chat-box .default::-webkit-scrollbar-thumb {
  background: #717280;
}

/* Handle on hover */
.chat-box .box-content::-webkit-scrollbar-thumb:hover,
.chat-box .default::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.chat-box .chat {
  margin-right: 15%;
}

.chat-box .chat.primary {
  margin-right: 0;
  margin-left: 15%;
  display: flex;
  justify-content: end;
}

.chat-box .chat p {
  display: inline-block;
  padding: 0.6em 1em;
  border-radius: var(--my-border-radius);
  background-color: white;
  color: black;
  line-height: 1.5em;
  font-size: var(--my-medium-font);
  margin: 0;
  transition: all 200ms;
}

.chat-box .chat.primary p {
  background-color: var(--my-primary);
  color: white;
}

/* lead */

.chat-box .lead {
  background-color: white;
  border-radius: var(--my-border-radius);
}

.chat-box .lead .title {
  display: flex;
  justify-content: space-between;
  padding-right: 16px;
  margin-top: 0.8em;
}

.chat-box .title p {
  padding-top: 0;
}

.chat-box .lead .input-div {
  background-color: white;
  display: flex;
  flex-direction: column;
  width: 100%;
  grid-gap: 8px;
  gap: 8px;
  margin-top: 8px;
  align-items: end;
}

.chat-box .lead .input-div > div {
  width: 100%;
  padding: 8px 16px;
  font-weight: 600;
}

.chat-box .lead form.input-div input {
  border: 1px solid #e2e2e2;
  background-color: #f4f4f5;
  width: 100%;
  border-radius: 4px;
  padding: 0.8em 1em;
  color: black;
  margin-top: 8px;
}

.chat-box .lead p {
  background-color: inherit;
  font-weight: 600;
}

.chat-box .lead button {
  background-color: black;
  padding: 8px 16px;
  margin: 16px;
  margin-top: 0;
  border-radius: 4px;
}

.chat-box .lead button:hover {
  transform: scale(1.1);
}

.chat-box .lead button svg {
  fill: white;
}

.chat-box .chat-footer {
  border-top: 1px solid rgba(179, 179, 179, 0.315);
  border-top: none !important;
  padding: 24px;
  padding-bottom: 0 !important;
  overflow-x: auto;
  width: 100%;
  /* background-color: #555; */
}

.chat-box .default {
  display: flex;
  grid-gap: 8px;
  gap: 8px;
  flex-wrap: nowrap;
  white-space: nowrap;
  overflow-x: auto;
}

.chat-box .default button {
  border: none;
  background-color: #27293c;
  border-radius: var(--my-border-radius);
  padding: 0.7em 1em;
  color: white;
  font-size: var(--my-medium-font);
  /* white-space: pre;
    display: block; */
}

.chat-box form {
  margin-top: 16px;
  display: grid;
  grid-template-columns: 1fr -webkit-max-content -webkit-max-content;
  grid-template-columns: 1fr max-content max-content;
  align-items: center;
  background-color: black;
  border-radius: var(--my-border-radius);
  grid-gap: 16px;
  gap: 16px;
}

.chat-box form input {
  padding: 1em 1.4em;
  background-color: transparent;
  border: none;
  color: white;
  display: block;
  outline: none !important;
  font-size: var(--my-medium-font);
}

.chat-box > hr {
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.144);
}

.chat-box .chat-footer hr {
  width: 1px;
  border: none;
  height: 24px;
  background-color: rgba(255, 255, 255, 0.295);
}

.chat-box form button {
  background-color: transparent;
  border: none;
  padding-right: 1em;
  padding-left: 1em;
  fill: white;
  transition: all 100ms;
}

.chat-box form button:hover {
  transform: scale(1.2);
}

.chat-box form button svg {
  width: 20px;
  height: 20px;
  fill: inherit;
}

.chat-box .power {
  font-size: 12px;
  text-align: center;
  color: var(--my-power-color);
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 4px;
  gap: 4px;
}

.chat-box .power a {
  font-weight: 600;
  color: inherit;
  text-decoration: none;
}

/* light mode */

/* dot progress */

.progress-load {
  display: flex;
  border-radius: 50%;
  flex-direction: row;
  grid-gap: 4px;
  gap: 4px;
}

.progress-load .item {
  font-size: var(--my-medium-font);
  width: 1em;
  height: 1em;
  scale: 0;
  border-radius: 50%;
  background: rgb(211, 211, 211);
  animation: loading_492 1s ease infinite;
  animation-delay: 0.5s;
}

.progress-load.dark .item {
  background: black;
}

@keyframes loading_492 {
  50% {
    scale: 1;
  }
}

.progress-load .item:nth-child(1) {
  animation-delay: 0.3s;
}

.progress-load .item:nth-child(2) {
  animation-delay: 0.6s;
}

.progress-load .item:nth-child(3) {
  animation-delay: 0.9s;
}

@media only screen and (max-width: 900px) {
  .chat-box .header {
    height: 80px;
    padding: 0 40px;
  }

  .chat-box .box-content {
    padding: 24px;
  }

  .chat-box .chat-footer {
    padding: 40px;
  }
}

@media only screen and (max-width: 700px) {
  .chat-box .header {
    height: 60px;
    padding: 0 12px;
  }

  .chat-box .header .logo {
    height: 32px;
  }

  .chat-box .box-content {
    padding: 24px 24px;
  }

  .chat-box .chat-footer {
    padding: 12px;
  }

  .chat-box .header button {
    width: 32px;
    height: 32px;
  }

  .chat-box .header button img {
    width: 20px;
    height: 20px;
  }

  .chat-box form button img {
    height: 20px;
  }

  .chat-box form {
    grid-gap: 0;
    gap: 0;
  }
}

/*css for assistant chat response*/
.chat-box.light .assistant {
  background-color: #f4f4f5;
}
.chat-box.light .source {
  background-color: white;
}
.chat-box .source,
.chat-box .assistant {
  border-radius: var(--my-border-radius);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  line-height: 1.5em;
  font-size: var(--my-medium-font);
  background-color: #f4f4f5;
  margin-right: 15%;
}
.chat-box .assistant {
  background-color: white;
}
.chat-box .assistant-box .source .strong-text {
  cursor: pointer;
}
.chat-box .assistant-box .source {
  padding-block: 0.8em 0.5em;
  padding-inline: 0.8em 1em;
  margin-block: 0em !important;
  color: black;
}

.chat-box .source .list-links {
  padding-block: 0.2em 0.6em;
  display: flex;
  flex-direction: row;
  grid-gap: 9px;
  gap: 9px;
}
.chat-box .assistant p,
.chat-box .assistant div {
  margin-block: 0em !important;
  padding-block: 0.8em;
  padding-inline: 0.8em 1em;
  color: black;
}

.assistant .list-item {
  display: flex;
  align-items: start;
  grid-gap: 10px;
  gap: 10px;
  position: relative;
}

.assistant .list-item::before {
  content: "";
  position: absolute;
  top: 30px;
  left: 31px;
  width: 1.5px;
  height: 100%;
  background-color: white;
}
.assistant .list-item:last-of-type::before {
  display: none;
}
.assistant .before-full-stop {
  color: black;
  background-color: white;
  border-radius: 50%;
  display: inline-block;
  min-width: 30px;
  min-height: 30px;
  text-align: center;
  line-height: 30px;
  position: inherit;
  font-size: 14px;
  overflow: hidden;
  margin: 0 5px;
}


