.invite-page {
    margin-top: 100px;
    --new-surface-color: #181924;
    margin-bottom: 100px;
}

.invite-page h2,
.invite-page h3 {
    color: white;
    font-size: var(--my-big-font);
    margin-top: 1em;
    margin-bottom: 1em;
    font-weight: normal;
}

.invite-page p {
    color: var(--my-color-on-surface2);
    font-size: var(--my-medium-font);
}

.invite-page .form-holder {
    margin-top: 50px;
    min-height: 300px;
    background-color: var(--new-surface-color);
    padding-bottom: 32px;
}

.invite-page .form-holder .title {
    height: 70px;
    background-color: var(--my-color-surface3);
    display: flex;
    align-items: center;
    padding: 0 32px;
}

.invite-page .form-holder .title img {
    height: 40px;
}


.invite-page form {
    margin-top: 50px;
    padding: 0 32px;
}

.invite-page form .inner {
    display: grid;
    grid-template-columns: 1.5fr 1fr;
    align-items: center;
    grid-gap: 50px;
    gap: 50px;
}

.invite-page form img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}


.invite-page .input-title {
    color: #8d99ae;
    font-size: var(--my-medium-font);
}



.invite-page input,
.invite-page textarea {
    padding: 1em 2em;
