    border: 1.5px solid #555768;
    background: rgba(85, 87, 104, 0.2);
    font-size: var(--my-medium-font);
    color: #eaeaec;
    width: 100%;
    outline: none;
    margin-bottom: 1em;
    resize: none;
}

.invite-page input:focus,
.invite-page textarea:focus {
    border-color: #a0a2ad;
}

.invite-page form button {
    font-size: var(--my-average-font2);
    text-align: center;
    display: block;
    width: 100%;
    padding: 1em;
    background-color: black;
    color: white;
    border: none;
    border-radius: var(--my-border-radius);
    margin-top: 50px;


}


@media only screen and (max-width: 1000px) {
    .invite-page form .inner {
        grid-template-columns: 1.5fr 1.2fr;
        grid-gap: 24px;
        gap: 24px;
    }
}


@media only screen and (max-width: 800px) {
    .invite-page form .inner {
        grid-template-columns: 100%;
    }

