:root {
  --my-primary: #05668d;
  --my-color-on-primary: white;
  --my-secondary: red;
  --my-color-on-secondary: red;
  --my-color-surface: #000000;
  --my-color-surface2: #1f202f;
  --my-color-surface3: #2b2d42;
  --my-color-on-surface: #717783;
  --my-color-on-surface2: #808B9E;
  --my-pink: #AA07E0;

  --my-medium-font: 16px;
  --my-small-font: 14px;
  --my-border-radius: 10px;
  --my-big-font: 32px;
  --my-big-font2: 28px;
  --my-average-font: 24px;
  --my-average-font2: 20px;
  --skeleton-color: rgba(255, 255, 255, 0.04);
  --page-gap: 24px;
  --max-page-width: 1280px;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: var(--my-color-surface);
}

.page a {
  text-decoration: none;
}

.page {

  --max-dd: max(0px, ((100% - var(--max-page-width)) * 0.5) - var(--page-gap) * 2);
  margin: 0 calc(var(--max-dd));
  color: var(--my-color-on-surface);
  font-family: "Raleway";

}

.page>* {
  margin-left: var(--page-gap);
  margin-right: var(--page-gap);
}

.page h1 {
  color: var(--my-color-on-primary);
}


button.primary{
  background-color: var(--my-primary) !important;
  color: white !important;
}



.page textarea {
  font-family: "Raleway";
}

.page .hover:hover {
  opacity: 0.7;
}



.page h3 {
  font-size: var(--my-average-font);
  margin-top: 1em;
  margin-bottom: 1em;
  font-weight: normal;
}

.page a,
.page p {
  font-size: var(--my-medium-font);

}

