.auto-popup {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* background-color: rgba(240, 248, 255, 0.05); */
  /* pointer-events: none; */
  display: flex;
  justify-content: center;
  align-items: flex-end;
  pointer-events: none;
  z-index: 10;
}


.auto-popup button {
  position: -webkit-sticky;
  position: sticky;
  bottom: 32px;
  /* align-items: center;
  justify-content: center; */
  border: none;
  height: -webkit-max-content;
  height: max-content;
  font-size: var(--my-medium-font);
  background-color: transparent;
  pointer-events: auto;
}

.auto-popup button>a {
  position: relative;
  padding: 8px;
  height: 56px;
  min-width: 56px;
  overflow: hidden;
  display: flex;
  align-items: center;
  border-radius: 50px;
  background-color: rgba(255, 255, 255, 0.18);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  color: white;
  scale: 0;
  transition: scale 200ms;
  text-decoration: none;

}


.auto-popup button>a.animate {
  animation-name: popup-start;
  animation-duration: 1.2s;
  scale: 1;
}

.auto-popup button>a.animate-rev {
  animation-name: popup-end;
  animation-duration: 1.2s;
}


@keyframes popup-start {
  0% {
    color: transparent;
    scale: 0;
    width: 56px;
  }

  50% {
    color: transparent;
    scale: 1;
    width: 56px;
  }

  100% {
    color: white;
    scale: 1;
    width: 236px;
  }
}

@keyframes popup-end {
  0% {
    scale: 1;
    width: 224px;
  }

  50% {
    scale: 1;
    width: 56px;
  }

  100% {
    scale: 0;
    width: 56px;
  }
}


.auto-popup span {
  padding-left: 20px;
  padding-right: 60px;
  white-space: pre;
}

.auto-popup button .icon {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #0071e3;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 8px;
  right: 8px;
}
