/* .dialog */
.simple-checkbox {
  position: relative;
  width: 60px;
  height: 38px;
}

.simple-checkbox:hover {
  opacity: 0.7;
}

.simple-checkbox .fill {
  width: 100%;
  height: 100%;
  background-color: #2b2d42;
  border-radius: 50px;
}

.simple-checkbox input {
  opacity: 0.001;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100% !important;
  display: block;

  z-index: 10;
}

.simple-checkbox input:checked ~ .fill {
  background-color: #036666;
}

.simple-checkbox input:checked ~ .thumb {
  left: unset;
  right: 5px;
}

.simple-checkbox .thumb {
  height: 28px;
  width: 28px;
  border-radius: 50%;
  position: absolute;
  left: 5px;
  top: 50%;
  transform: translateY(-50%);
  background-color: white;
}

@media only screen and (max-width: 700px) {
  .simple-checkbox {
    width: 40px;
    height: 28px;
  }

  .simple-checkbox .thumb {
    height: 20px;
    width: 20px;
  }
}

