.slider .slider-holder{
    width: 100%;
    position: relative;
    min-height: 10px;
}

.slider input{
    opacity: 0.001;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 10;
    top: 50%;
    transform: translateY(-50%);
    margin: 0;
    padding: 0;
    
}

.slider .fill, .slider .outline{
    background-color: white;
    height: 6px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    width: 50%;
}

.slider .outline{
    width: 100%;
    background-color: rgba(255, 255, 255, 0.11);
}



.slider .fill::after{
    content: '';
    width: 18px;
    height: 18px;
    display: block;
    position: absolute;
    transform: translate(-10px, -50%);
    top: 50%;
    left: 100%;
    background-color: white;
    border-radius: 50%;
}



